/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 111px;
  z-index: 200;
}

.header:before {
  content: "";
  position: absolute;
  z-index: -1;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background: #111626;
  opacity: 0;
  transition: opacity .3s linear;
}

.header.is-fixed:before {
  opacity: 1;
}

.header.is-fixed .header__logo {
  transition: transform .3s linear;
  transform: translate(0, 33px) scale(0.66);
}

.header.is-fixed .header__nav {
  transform: translate(-213px, 37px);
  transition: transform .3s linear;
}

.header.is-fixed .header__button {
  transform: translate(0, 0);
  opacity: 1;
}

.header .container {
  position: relative;
}

.header__button {
  position: absolute;
  right: 0;
  top: 35px;
  transform: translate(100%, 0);
  opacity: 0;
  transition: all .3s linear;
}

.header__logo {
  float: left;
  transform: translate(0, 52px);
  transition: transform .3s linear;
  transform-origin: 0 0;
}

.header__logo img {
  display: block;
}

.header__nav {
  float: right;
  transform: translate(0, 45px);
  position: relative;
  white-space: nowrap;
  transition: transform .3s linear;
}

.header__nav:before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(255, 255, 255, 0.2);
  height: 1px;
}

.header__nav li {
  display: inline-block;
  margin-right: 22px;
}

.header__nav li:last-child {
  margin-right: 0;
}

.header__nav a {
  display: block;
  height: 40px;
  line-height: 40px;
  padding: 0 3px;
  text-transform: uppercase;
  color: #fff;
  font-size: 14px;
  text-decoration: none;
  font-family: 'light',sans-serif;
}

.header__nav a:hover {
  position: relative;
  color: #b1ce01;
}

.header__nav a:hover:before {
  content: "";
  position: absolute;
  bottom: 0;
  height: 2px;
  background: #b1ce01;
  right: 0;
  width: 100%;
  left: 0;
}

.header__nav a.is-active {
  font-family: 'bold',sans-serif;
  color: #b1ce01;
  position: relative;
}

.header__nav a.is-active:before {
  content: "";
  position: absolute;
  bottom: 0;
  height: 2px;
  background: #b1ce01;
  right: 0;
  width: 100%;
  left: 0;
}

