.schedule-section__download span {
  text-decoration: underline;
}

.schedule-slide {
  margin: 0 60px;
}

.s {
  width: 700px;
  table-layout: fixed;
}

.s caption {
  text-align: left;
  padding-bottom: 5px;
}

.s__time {
  background: #f3f3f3;
  font-size: 22px;
  color: #181f32;
  width: 114px;
  padding: 6px 18px;
}

.s__time span {
  display: block;
  text-align: right;
  font-size: 14px;
}

.s__v {
  position: relative;
  width: 20px;
  height: 20px;
  display: block;
}

.s__v span {
  position: absolute;
  right: 0;
  top: 0;
  text-transform: uppercase;
  font-family: 'light',sans-serif;
  color: #c0c0c0;
  font-size: 14px;
  display: block;
  transform: rotate(-90deg);
  transform-origin: 100% 100%;
}

.s thead td, .s thead th {
  background: #b1ce01;
  color: #fff;
  font-size: 14px;
  padding-left: 26px;
  padding-top: 4px;
  padding-bottom: 4px;
  text-align: left;
}

.s thead .s__time {
  text-align: center;
}

.s__content {
  width: 40%;
  background: #f3f3f3;
  padding: 10px 25px;
  color: #181e33;
}

.s__content .s__level {
  float: right;
  text-transform: uppercase;
  font-size: 11px;
  color: #b1ce01;
  line-height: 1.6;
}

.s__content strong {
  text-transform: uppercase;
  font-family: 'bold',sans-serif;
  display: block;
  max-width: 6em;
  line-height: 1;
  margin-bottom: 4px;
}

.s__content small {
  font-family: 'light',sans-serif;
  display: block;
}

.s__first {
  width: 33px;
  overflow: hidden;
}

.s tbody td, .s tbody th {
  border-bottom: 1px solid #DDDDDF;
  border-right: 1px solid #DDDDDF;
}

.s__lunch {
  font-size: 19px;
  text-transform: uppercase;
  color: #a4c100;
  padding-left: 25px;
}

.s__break td, .s__break th {
  background: #fff;
  padding-top: 15px;
  padding-bottom: 15px;
  border-right-color: #fff !important;
}

.s__break .s__time {
  font-size: 13px;
  color: #111626;
  white-space: nowrap;
}

.s tfoot td {
  background: #b1ce01;
  color: #fff;
}

.s tfoot .s__first {
  background: #fff;
}

.s tfoot .s__time {
  font-family: 'semibold',sans-serif;
  font-size: 13px;
  white-space: nowrap;
  color: #fff;
  text-transform: uppercase;
}

.s tfoot .s__party {
  font-size: 20px;
  font-family: 'light',sans-serif;
  color: #fff;
  text-transform: uppercase;
  line-height: 1.3;
}

.s tfoot .s__program {
  font-size: 14px;
  text-transform: uppercase;
  color: #fff;
}

.s tfoot td:last-child {
  text-align: right;
}

.schedule__note {
  font-size: 12px;
  margin-top: 10px;
  color: #c0c0c0;
}

.artists {
  background: #f6f6f6;
  margin: 230px 0 100px 0;
}

.artists .container {
  padding: 47px 0 110px 0;
  position: relative;
}

.artists .container .h2 {
  position: absolute;
  right: 0;
  top: -1.6em;
  text-align: right;
}

.artists-slider {
  position: relative;
}

.artists-slider .slick-hidden {
  display: none;
}

.artists-slider__next, .artists-slider__prev {
  position: absolute;
  right: 10px;
  bottom: -30px;
}

.artists-slider__prev {
  right: 80px;
  transform: rotate(-180deg);
}

.artist__photo {
  margin-bottom: 20px;
  margin-right: 40px;
}

.artist__photo img {
  width: 100%;
  display: block;
}

.artist__country {
  font-size: 14px;
  color: #1b2237;
  padding-right: 10px;
}

.artist__title {
  font-family: 'blackfont',sans-serif;
  font-size: 20px;
  color: #1b2237;
  text-transform: uppercase;
  padding-right: 10px;
}

.tabs {
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  font-family: 'light',sans-serif;
  text-align: center;
  margin-bottom: 30px;
}

.tabs_right {
  text-align: right;
}

.tabs_right a:last-child {
  padding-right: 0;
}

.tabs a {
  display: inline-block;
  color: #111626;
  text-decoration: none;
  font-size: 18px;
  text-transform: uppercase;
  padding: 0 20px;
  padding-bottom: 20px;
}

.tabs a.is-active {
  font-family: 'bold',sans-serif;
}

.footer {
  background: #f6f6f6 url(../img/footer.png) no-repeat 50% 0;
  text-align: left;
  font-size: 14px;
  padding-top: 180px;
  padding-bottom: 20px;
}

.footer p {
  margin-bottom: .5em;
}

.footer a {
  color: #000;
}
